/*----------------------------
            SLIDER(sign up)
----------------------------*/


slider2{
  display: block;
  width: 100%;
  height: 100%;
  background-color: rgb(77, 76, 76);
  overflow: hidden;
  position: fixed;
}

slider2 > *{
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  background-color: rgb(77, 76, 76);
  animation: slide 12s infinite;
  overflow: hidden;
}

slide2:nth-child(1){
  left: 0%;
  animation-delay: 9s;
  background-image: url(/static/media/img1.ef8dd9c9.jpg);
  background-size: cover;
  background-position: center;
  
}

slide2:nth-child(2) {
  animation-delay: 6s;
  background-image: url(/static/media/img2.4cee93f3.jpg);
  background-size: cover;
  background-position: center;
  
}

slide2:nth-child(3) {
  animation-delay: 3s;
  background-image: url(/static/media/img3.fb4dafe0.jpg);
  background-size: cover;
  background-position: center;
  
}

slide2:nth-child(4) {
  left: 0%;
  animation-delay: 0s;
  background-image: url(/static/media/img4.003c8496.jpg);
  background-size: cover;
  background-position: center;
  
}

@keyframes slide2{
  0%{left:100%; width:100%;}
  5%{left:0%;}
  25%{left: 0%;}
  30%{left:-100%; width:100%;}
  30.0001%{left:-100%; width:0%;}
  100%{left:100%; width:0%;}
}

slide2 h1{
  color: #fff;
  text-align: center;
}


/*----------------------------
            SLIDER(sign in)
----------------------------*/


slider {
  display: block;
  width: 100%;
  height: 100%;
  background-color: rgb(77, 76, 76);
  overflow: hidden;
  position: fixed;
}

slider>* {
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  background-color: rgb(77, 76, 76);
  animation: slide 12s infinite;
  overflow: hidden;
}

slide:nth-child(1) {
  left: 0%;
  animation-delay: 9s;
  background-image: url(/static/media/img5.b3138dc8.jpg);
  background-size: cover;
  background-position: center;

}

slide:nth-child(2) {
  animation-delay: 6s;
  background-image: url(/static/media/img6.c9daaa4f.jpg);
  background-size: cover;
  background-position: center;

}

slide:nth-child(3) {
  animation-delay: 3s;
  background-image: url(/static/media/img7.9bd26600.jpg);
  background-size: cover;
  background-position: center;

}

slide:nth-child(4) {
  left: 0%;
  animation-delay: 0s;
  background-image: url(/static/media/img8.c22796ee.jpg);
  background-size: cover;
  background-position: center;

}

@keyframes slide {
  0% {
    left: 100%;
    width: 100%;
  }

  5% {
    left: 0%;
  }

  25% {
    left: 0%;
  }

  30% {
    left: -100%;
    width: 100%;
  }

  30.0001% {
    left: -100%;
    width: 0%;
  }

  100% {
    left: 100%;
    width: 0%;
  }
}

slide h1 {
  color: #fff;
  text-align: center;
}



/*----------------------------
            NAVBAR
----------------------------*/


.nav-bar{
  font-family: 'Titillium Web',
  sans-serif;
  font-size: 20px ;
  margin-right: 10px;
}

nav ul a {
  transition: background-color .3s;
  font-size: 1.3rem;
  color: rgb(8, 3, 3);
  display: block;
  padding: 0 25px;
  cursor: pointer;
  background-color: #26a69a;
}

nav {
  color: #fff;
  background-color: #26a69a;
  width: 100%;
  height: 80px;
  line-height: 80px;
}

.btn-flat {
  
  color: #e70d0d;
  
}

ul {
  display: block;
  list-style-type: disc;
  -webkit-margin-before: 0em;
          margin-block-start: 0em;
  -webkit-margin-after: 0em;
          margin-block-end: 0em;
  -webkit-margin-start: 0px;
          margin-inline-start: 0px;
  -webkit-margin-end: 0px;
          margin-inline-end: 0px;
  -webkit-padding-start: 0px;
          padding-inline-start: 0px;
}

nav i,
nav [class^="mdi-"], nav [class*="mdi-"],
nav i.material-icons {
  display: block;
  font-size: 44px;
  height: 56px;
  line-height: 56px;
  position:relative;
  top: 13px;
}

